<template>
  <div>
    <!-- 头部标题 -->
    <div>
      <van-nav-bar fixed>
        <template #left>
          <img src="@/assets/logo.png" alt="" class="logo" />
        </template>
        <template #right>
          <van-icon name="search" size="0.54rem" color="#fff" @click="moveSearchFn" />
        </template>
      </van-nav-bar>
    </div>

    <!-- 内容区域 -->
    <div class="main">
      <!-- tab导航 -->
      <van-tabs v-model="channelId" animated sticky offset-top="0.46rem">
        <van-tab v-for="item in channelsList" :key="item.id" :title="item.name" :name="item.id">
          <!-- 内容列表组件 -->
          <ArticleList :channelId="channelId"></ArticleList>
        </van-tab>
      </van-tabs>

      <!-- 添加图标 -->
      <van-icon name="plus" size="0.37333334rem" class="moreChannels" @click="show = true" />
    </div>

    <!-- 频道点击+号弹出层 -->
    <van-popup v-model="show" get-container="body">
      <!-- 弹出的内容组件 -->
      <ChannelEdit v-model="channelId" :userList="channelsList" :unCheckLIst="unCheckChannelList"
        @addChannelEv="addChannelFn" @removeChannelEV="removeChannelFn" @closeEv="closeFn">
      </ChannelEdit>
    </van-popup>
  </div>
</template>

<script>
// 导入获取用户列表、文章获取的API接口和所有频道的API和更新频道的接口
import { getUserChannelsAPI, getAllChannelsAPI, updateChannelAPI } from '@/api/index'
// 导入ArticleList组件渲染到导航栏下的内容区域
import ArticleList from './components/ArticleList.vue'

import ChannelEdit from './ChannelEdit.vue'

export default {
  name: 'Home',
  components: {
    ArticleList,
    ChannelEdit
  },
  data() {
    return {
      // channelId值双向绑定对应的频道id
      channelId: 0,
      channelsList: [], // 用户已选展示在首页的频道列表
      allChannelList: [], // 所有频道列表
      show: false
    }
  },
  methods: {
    // 子组件自定义的添加频道事假触发的方法
    async addChannelFn(channelObj) {
      this.channelsList.push(channelObj)

      // 调用更新频道的API
      await updateChannelAPI({
        channels: this.channelsList
      })
    },
    // 子组件自定义的移除频道事件触发的方法
    async removeChannelFn(channelObj) {
      this.channelsList = this.channelsList.filter(channel => {
        if (channelObj.name === '推荐') {
          // 不能删除推荐的频道
          return true
        }
        return channelObj.id !== channel.id
      })
      await updateChannelAPI({
        channels: this.channelsList
      })
    },
    // 触发关闭频道选择面板的方法
    closeFn() {
      this.show = false
    },
    // 放大镜点击事件,跳转到search路由对应的组件
    moveSearchFn() {
      this.$router.push('/search')
    }
  },
  computed: {
    // 没有选择的频道
    unCheckChannelList() {
      // 剔除所有频道中存在的用户已选的频道，把结果放到新数组中
      const unCheckChannelList = this.allChannelList.filter(bigObj => {
        // findIndex方法遍历用户频道数组
        const index = this.channelsList.findIndex(smallObj => {
          // findIndex遍历数组，查找满足return的条件，真就返回该值的索引，否则返回-1
          return smallObj.id === bigObj.id
        })
        if (index > -1) {
          // >-1即所有频道中该频道存在于用户频道中，返回false，filter不会把该值放在新数组中
          return false
        } else {
          return true
        }
      })
      // unCheckChannelList方法最终返回的数组
      return unCheckChannelList
    }
  },
  async created() {
    // 请求用户已选频道列表数据
    const { data: res } = await getUserChannelsAPI()
    this.channelsList = res.data.channels

    // 所有频道数据请求
    const { data: res2 } = await getAllChannelsAPI()
    this.allChannelList = res2.data.channels
  }
}
</script>

<style lang="less" scoped>
.logo {
  width: 100px;
  height: 30px;
}

.main {
  padding-top: 46px;
}

/deep/ .van-tabs__wrap {
  padding-right: 30px;
  background-color: #fff;
}

// 设置小图标的样式
.moreChannels {
  position: fixed;
  top: 62px;
  right: 8px;
  z-index: 999;
}

// 频道添加出现的面板样式
.van-popup {
  width: 100vw;
  height: 100vh;
}
</style>
