<template>
  <div>
    <!-- 下拉刷新组件 -->
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <!-- 上拉加载更多组件 -->
      <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad" :immediate-check="false"
        offset="50">
        <!-- 文章列表 -->
        <ArticleItem v-for="item in list" :key="item.art_id" :artObj="item" @dislikeEV="dislikeFn" @reportEV="reportFn"
          @click.native="itemClickFn(item.art_id)">
        </ArticleItem>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import ArticleItem from '../../../components/ArticleItem.vue'
import { articlesListAPI, dislikeArticle, articleReportsAPI } from '@/api/index'
import { Notify } from 'vant'

export default {
  name: 'ArticleList',
  props: {
    channelId: Number
  },
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
      thetime: new Date().getTime(),
      isLoading: false
    }
  },
  methods: {
    // 上拉加载事件方法
    async getArticleList() {
      // 负责拿文章列表数据
      const res = await articlesListAPI({
        channel_id: this.channelId,
        timestamp: this.thetime
      })

      this.thetime = res.data.data.pre_timestamp // 保存下一页数据的时间(做分页用)
      this.list = [...this.list, ...res.data.data.results]
      // 等待网络请求结果后
      this.loading = false // 底部加载更多状态

      // 如果无数据
      if (res.data.data.pre_timestamp === null) {
        this.finished = true // 告诉list组件直接底部显示没有更多数据
      }
    },
    // 上拉加载
    onLoad() {
      if (this.list.length === 0) {
        this.loading = false
        return
      }
      // 有第一页数据以后, onLoad才再发请求
      if (this.list.length > 0) {
        this.getArticleList()
      }
    },
    // 下拉刷新
    async onRefresh() {
      // 刷新清空原文章列表数组
      this.thetime = new Date().getTime()
      const { data: res } = await articlesListAPI({
        channel_id: this.channelId,
        timestamp: this.thetime
      })
      this.thetime = res.data.pre_timestamp
      this.list = res.data.results
      this.isLoading = false
    },
    // 自定义不感兴趣文章事件的触发方法
    async dislikeFn(artId) {
      try {
        const { data: res } = await dislikeArticle({ artId })
        console.log(res)
        Notify({ type: 'success', message: '反馈成功' })
      } catch (err) {
      }
    },
    // 反馈垃圾内容的方法
    async reportFn(artId, value) {
      try {
        const { data: res } = await articleReportsAPI({ artId, value })
        console.log(res)
        Notify({ type: 'success', message: '举报成功' })
      } catch (err) {
      }
    },
    // 文章组件点击事件
    itemClickFn(id) {
      this.$router.push({
        path: `/detail?art_id=${id}`
      })
    }
  },
  components: {
    ArticleItem
  },
  created() {
    this.getArticleList()
  }
}
</script>

