<template>
  <div>
    <!-- 弹出层的头部区域 -->
    <van-nav-bar title="频道管理">
      <template #right>
        <van-icon name="cross" size="0.37333334rem" color="white" @click="closeFn" />
      </template>
    </van-nav-bar>
    <!-- 我的频道 -->
    <div class="my-channel-box">
      <div class="channel-title">
        <span>我的频道
          <span class="small-title">
            {{ isEdit ? '点击删除频道' : '点击进入频道' }}
          </span>
        </span>
        <span @click="isEdit = !isEdit">{{ isEdit ? '完成' : '编辑' }}</span>
      </div>
      <!-- 我的频道列表 -->
      <van-row type="flex">
        <van-col span="6" v-for="item in userList" :key="item.id" @click="removeChannel(item)">
          <div class="channel-item van-hairline--surround">
            {{ item.name }}
            <!-- 删除的徽标 -->
            <van-badge color="transparent" class="cross-badge" v-if="isEdit && item.name !== '推荐'">
              <template #content>
                <van-icon name="cross" class="badge-icon" color="#cfcfcf" size="0.32rem" />
              </template>
            </van-badge>
          </div>
        </van-col>
      </van-row>
    </div>

    <!-- 更多频道 -->
    <div class="more-channel-box">
      <div class="channel-title">
        <span>点击添加更多频道：</span>
      </div>
      <!-- 更多频道列表 -->
      <van-row type="flex">
        <van-col span="6" v-for="item in unCheckLIst" :key="item.id" @click="moreFn(item)">
          <div class="channel-item van-hairline--surround">{{ item.name }}</div>
        </van-col>
      </van-row>
    </div>
  </div>
</template>

<script>

export default {
  name: 'ChannelEdit',
  // 接收来自index.vue传来的频道列表参数
  props: {
    userList: { type: Array },
    unCheckLIst: { type: Array }
  },
  data() {
    return {
      // 频道编辑的状态
      isEdit: false
    }
  },
  methods: {
    // 更多频道的点击事件
    moreFn(channelObj) {
      // 先判断用户是否先点击了编辑，点击后才能添加
      if (this.isEdit) {
        // 然后把要添加的频道的数据传出去
        this.$emit('addChannelEv', channelObj)
      }
    },
    // 移除频道方法
    removeChannel(channelObj) {
      // 先判断是否处于编辑状态，是就移除频道的事件
      if (this.isEdit) {
        this.$emit('removeChannelEV', channelObj)
      } else {
        // 否则，点击谁就关闭该弹出层，并且外面显示正在浏览的频道就是该频道
        this.$emit('input', channelObj.id)
        this.closeFn()
      }
    },
    // 关闭频道弹出层按钮方法
    closeFn() {
      this.isEdit = false
      // 点击触发自定义的关闭事件，index.vue里再处理事件触发的方法
      this.$emit('closeEv')
    }
  }
}
</script>

   <style scoped lang="less">
.van-popup,
.popup-container {
  background-color: transparent;
  height: 100%;
  width: 100%;
}

.popup-container {
  display: flex;
  flex-direction: column;
}

.pop-header {
  height: 90px;
  background-color: #007bff;
  color: white;
  text-align: center;
  font-size: 14px;
  position: relative;

  .title {
    width: 100%;
    position: absolute;
    bottom: 15px;
  }
}

.pop-body {
  flex: 1;
  overflow: scroll;
  padding: 8px;
  background-color: white;
}

.my-channel-box,
.more-channel-box {
  .channel-title {
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    line-height: 28px;
    padding: 0 6px;
  }
}

.channel-item {
  font-size: 12px;
  text-align: center;
  line-height: 36px;
  background-color: #fafafa;
  margin: 5px;
}

/*删除的微标 */
.cross-badge {
  position: absolute;
  right: -3px;
  top: 0;
  border: none;
}

/*提示文字 */
.small-title {
  font-size: 10px;
  color: gray;
}
</style>
