<template>
  <div>
    <!-- 一条文章单元格 -->
    <van-cell>
      <!-- 标题区域的插槽 -->
      <template #title>
        <div class="title-box">
          <!-- 标题 -->
          <span>{{ artObj.title }}</span>
          <!-- 单图图片 -->
          <img v-if="artObj.cover.type === 1" :src="artObj.cover.images" alt="" class="thumb">
        </div>

        <!-- 三图 -->
        <div class="thumb-box" v-if="artObj.cover.type > 1">
          <img class="thumb" :src="imgUrl" v-for="(imgUrl, index) in artObj.cover.images" :key="index" />
        </div>
      </template>
      <!-- label 区域的插槽 -->
      <template #label>
        <div class="label-box">
          <div>
            <span>{{ artObj.aut_name }}</span>
            <span>{{ artObj.comm_count }}评论</span>
            <span>{{ formatTime(artObj.pubdate) }}</span>
          </div>
          <!-- 反馈按钮 -->
          <van-icon name="cross" @click.stop="show = true" v-if="SearchParam" />
        </div>
      </template>
    </van-cell>

    <!-- 反馈面板,和反馈按钮绑定 -->
    <van-action-sheet v-model="show" :actions="actions" @select="onSelect" get-container="body"
      :cancel-text="bottomText" @cancel="cancelFn" @close="closeFn" />
  </div>
</template>

<script>
// 导入封装的时间处理的方法
import { timeAgo } from '@/utils/date.js'
// 导入actions的数据
import { firstActions, secondActions } from '@/api/report'

export default {
  name: 'ArticleItem',
  props: {
    artObj: Object,
    SearchParam: { default: true }
  },
  data() {
    return {
      // 自定义了一个方法，就是导入来的时间处理函数。这里我们没有放在methods中，data中一样可以使用
      formatTime: timeAgo,
      show: false,
      actions: firstActions,
      bottomText: '取消'
    }
  },
  methods: {
    // 反馈面板点击事件
    onSelect(action) {
      if (action.name === '反馈垃圾内容') {
        this.actions = secondActions
        this.bottomText = '返回'
      } else if (action.name === '不感兴趣') {
        // 父组件文章列表有不喜欢文章的API，通过自定义事件向其传不喜欢文章的id过去
        // 每个文章是一个独立的组件，只是被渲染到列表中有很多，所以这里点的谁，this就是哪个文章的组件
        this.$emit('dislikeEV', this.artObj.art_id)
        // 子向父传值自定义事件
        // 随后关闭反馈面板
        this.show = false
      } else {
        // 点击二级反馈面板的选项
        this.$emit('reportEV', this.artObj.art_id, action.value)
        this.show = false
      }
    },
    // 点击取消或返回按钮触发
    cancelFn() {
      if (this.bottomText === '返回') {
        this.actions = firstActions
        this.bottomText = '取消'
        this.show = true
      }
    },
    // 点击遮罩时触发的关闭事件方法
    closeFn() {
      this.actions = firstActions
      this.bottomText = '取消'
    }
  }
}
</script>

   <style scoped lang="less">
/* 标题样式 */
.title-box {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

/* label描述样式 */
.label-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* 文章信息span */
.label-box span {
  margin: 0 3px;

  &:first-child {
    margin-left: 0;
  }
}

// 单图
.thumb {
  width: 113px;
  height: 70px;
  background-color: #f8f8f8;
  object-fit: cover;
}

/* 三图, 图片容器 */
.thumb-box {
  display: flex;
  justify-content: space-between;
}
</style>
